import { Routes, RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';

import { HydroponicFodderComponent } from './hydroponicFodder.component';
import { MustandSpinachComponent } from './mustandSpinach.component';
import { ProductsComponent } from './products.component';

const routes: Routes = [
    {
        path: 'products', component: ProductsComponent,
        children: [
            {path:'', redirectTo:'/products/fodder',pathMatch:'full'},
            { path: 'fodder', component: HydroponicFodderComponent },
            { path: 'spinach', component: MustandSpinachComponent }
        ]
    }

];
@NgModule({
    imports: [
        RouterModule.forChild(routes)
    ],
    exports: [RouterModule]
})
export class ProductRoutes { }


